import React from "react";

export default function Counter() {
  let [count, update_count] = React.useState(0);
  return (
    <div className="App">
      <h1>Count:{count}</h1>
      <button className="btn" onClick={() => update_count(count + 1)}>
        Increase
      </button>
      <button
        className="btn"
        disabled={count <= 0 ? true : false}
        onClick={() => update_count(count > 0 ? count - 1 : (count = 0))}
      >
        Decrease
      </button>
      <button className="btn" onClick={() => update_count((count = 0))}>
        Reset
      </button>
    </div>
  );
}
